.m-overlay,
.modal-overlay {
    align-items: center;
    bottom: 0;
    box-sizing: border-box;
    display: none;
    justify-content: center;
    left: 0;
    min-width: 320px;
    padding: 16px;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 999999
}

@media (max-width:767px) {

    .m-overlay,
    .modal-overlay {
        align-items: unset;
        padding: 0
    }
}

.m-overlay:before,
.modal-overlay:before {
    background-color: #000000b3;
    bottom: 0;
    content: "";
    display: none;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    will-change: opacity
}

.m-overlay:before,
.m-overlay>div,
.modal-overlay:before,
.modal-overlay>div {
    animation-duration: .35s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out
}

.m-overlay>div,
.modal-overlay>div {
    background-color: #fff;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    max-height: calc(100% - 2.5rem);
    max-width: 580px;
    overflow: auto;
    position: relative;
    width: 100%;
    will-change: transform
}

@media (max-width:767px) {

    .m-overlay>div,
    .modal-overlay>div {
        border-radius: 0;
        max-height: 100%;
        max-width: 100%
    }
}

.m-overlay.opening,
.modal-overlay.opening {
    display: flex
}

.m-overlay.opening:before,
.modal-overlay.opening:before {
    animation-name: fade-in;
    display: block
}

.m-overlay.open,
.modal-overlay.open {
    display: flex
}

.m-overlay.open>div,
.modal-overlay.open>div {
    animation-name: zoom-in
}

@media (max-width:767px) {

    .m-overlay.open>div,
    .modal-overlay.open>div {
        animation-name: slide-up
    }
}

.m-overlay.open:before,
.modal-overlay.open:before {
    display: block
}

.m-overlay.closing,
.modal-overlay.closing {
    display: flex
}

.m-overlay.closing>div,
.modal-overlay.closing>div {
    animation-name: zoom-out
}

@media (max-width:767px) {

    .m-overlay.closing>div,
    .modal-overlay.closing>div {
        animation-name: slide-down
    }
}

.m-overlay.closing:before,
.modal-overlay.closing:before {
    animation-name: fade-out;
    display: block
}

@keyframes fade-out {
    0% {
        opacity: 1
    }

    to {
        opacity: 0
    }
}

@keyframes slide-up {
    0% {
        transform: translateY(100%)
    }

    to {
        transform: translateY(0)
    }
}

@keyframes slide-down {
    0% {
        transform: translateY(0)
    }

    to {
        transform: translateY(100%)
    }
}

@keyframes zoom-in {
    0% {
        transform: scale(0)
    }

    to {
        transform: scale(1)
    }
}

@keyframes zoom-out {
    0% {
        transform: scale(1)
    }

    to {
        transform: scale(0)
    }
}

