    color: #051f4e;
    font-size: 3.125rem;
    font-weight: 700;
    letter-spacing: -.09375rem;
    line-height: 3.5rem;
    margin-block: -.625rem -.5rem;
    padding-bottom: 1.625rem;
    transition: transform .5s ease;
    will-change: transform
}

