        font-size: 1.5rem;
        line-height: 2.25rem;
        margin-block: -.625rem -.5rem;
        padding-bottom: .5rem
    }
}

section.accordion-with-animation>div>ul {
    grid-gap: .625rem;
    display: grid
}

section.accordion-with-animation>div>ul>li {
    border-radius: 1rem;
    color: #051f4e;
    font-size: 1rem;
    line-height: 1.5rem;
    position: relative;
    transition-property: color, background-color
}

 section.accordion-with-animation>div>ul>li:hover {
    background-color: #d0d8ff;
    color: #153bff
}

section.accordion-with-animation>div>ul>li.open {
    background-color: #051f4e;
    color: #fff;
    transition-duration: .3s;
    transition-timing-function: ease-in
}

 section.accordion-with-animation>div>ul>li.open:hover {
    background-color: #051f4e;
    color: #fff
}

section.accordion-with-animation>div>ul>li.open .panel {
    height: auto;
    transition-duration: .3s;
    transition-timing-function: ease-in
}

section.accordion-with-animation>div>ul>li.open .panel p {
    color: #fff;
    transition-duration: .3s;
    transition-timing-function: ease-in
}

section.accordion-with-animation>div>ul>li.open .panel a:focus-visible {
    outline-color: #fff
}

section.accordion-with-animation>div>ul>li.open .chevron {
    background-color: #fff;
    transform: rotate(-180deg);
    transition-duration: .3s;
    transition-timing-function: ease-in
}

section.accordion-with-animation>div>ul>li.open .chevron svg {
    color: #051f4e;
    transition-duration: .3s;
    transition-timing-function: ease-in
}

section.accordion-with-animation>div>ul>li.closing {
    background: #fff;
    color: #051f4e;
    transition-duration: .5s;
    transition-timing-function: ease-out
}

section.accordion-with-animation>div>ul>li.closing:hover {
    background-color: #d0d8ff;
    color: #153bff
}

section.accordion-with-animation>div>ul>li.closing .panel {
    transition-duration: .5s;
    transition-timing-function: ease-out
}

section.accordion-with-animation>div>ul>li.closing .panel p {
    color: #051f4e;
    transition-duration: .5s;
    transition-timing-function: ease-out
}

section.accordion-with-animation>div>ul>li.closing .chevron {
    background-color: #153bff;
    transform: rotate(0deg);
    transition-duration: .5s;
    transition-timing-function: ease-out
}

section.accordion-with-animation>div>ul>li.closing .chevron svg {
    color: #fff;
    transition-duration: .5s;
    transition-timing-function: ease-out
}

section.accordion-with-animation button {
    grid-gap: 1.5rem;
    align-items: center;
    display: grid;
    font-weight: 600;
    grid-auto-flow: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    text-align: left;
    width: 100%
}

section.accordion-with-animation button:focus {
    outline: none
}

section.accordion-with-animation button:focus:before {
    opacity: 1
}

section.accordion-with-animation button:before {
    border: .125rem solid #153bff;
    border-radius: 1rem;
    content: "";
    display: block;
    inset: -.25rem;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    will-change: opacity
}

section.accordion-with-animation .text {
    margin-block: -.3125rem
}

section.accordion-with-animation .chevron {
    background-color: #153bff;
    border-radius: 50%;
    display: block;
    height: 1.5rem;
    width: 1.5rem;
    will-change: transform
}

section.accordion-with-animation .chevron svg {
    color: #fff
}

section.accordion-with-animation .chevron svg * {
    stroke: currentColor
}

section.accordion-with-animation .panel {
    height: 0;
    overflow: hidden;
    transition-property: background-color, color, height
}

section.accordion-with-animation .panel>div {
    padding: 0 24px 1rem
}

section.accordion-with-animation .panel>div p {
    transition-property: color
}

section.accordion-with-animation .panel a {
    font-weight: 700;
    text-decoration: underline
}

section.accordion-with-animation .panel a:focus-visible {
    border-radius: .25rem;
    outline: .125rem solid #153bff;
    outline-offset: .0625rem
}

section.accordion-with-animation>div>div {
    grid-gap: 1rem;
    display: grid;
    grid-auto-flow: column;
    justify-content: flex-start
}

@media (max-width:767px) {
    section.accordion-with-animation>div>div {
        grid-auto-flow: row;
        justify-content: stretch
    }
}

